<template>
<view>
	<view @click="navigator(item.id)" class="news_item" v-for="(item,index) in list" :key="index">
		<image :src='item.img_url' mode="widthFix"></image>
		<view class="right">
			<view class="tit">
				 {{item.title}}
			</view>
			<view class="info">
				<text>发表时间:{{item.release_time}}</text>
				<text>浏览:{{item.browse}}</text>
			</view>
		</view>
	</view>
</view>
</template>

<script>
	export default {
		props: ['list'],
		methods:{
			navigator (id) {
				this.$emit('itemClick',id)
			}
		}
	}
</script>

